@font-face {
    font-family: 'iconfont';
    src: url('../icon-font/iconfont.eot?71890918');
    src: url('../icon-font/iconfont.eot?71890918#iefix') format('embedded-opentype'),
         url('../icon-font/iconfont.woff2?71890918') format('woff2'),
         url('../icon-font/iconfont.woff?71890918') format('woff'),
         url('../icon-font/iconfont.ttf?71890918') format('truetype'),
         url('../icon-font/iconfont.svg?71890918#iconfont') format('svg');
    font-weight: normal;
    font-style: normal;
  }
  /* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
  /* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
  /*
  @media screen and (-webkit-min-device-pixel-ratio:0) {
    @font-face {
      font-family: 'iconfont';
      src: url('../font/iconfont.svg?71890918#iconfont') format('svg');
    }
  }
  */
   
   [class^="icon-"]:before, [class*=" icon-"]:before {
    font-family: "iconfont";
    font-style: normal;
    font-weight: normal;
   
    display: inline-block;
    text-decoration: inherit;
   
    /* For safety - reset parent styles, that can break glyph codes*/
    font-variant: normal;
    text-transform: none;
   
    /* you can be more comfortable with increased icons size */
    /* font-size: 120%; */
   
    /* Font smoothing. That was taken from TWBS */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
   
    /* Uncomment for 3D effect */
    /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
  }
   
